<template>
    <div>
        <LoginTutorial></LoginTutorial>
        <h1>&nbsp;&nbsp;&nbsp;&nbsp;Login In</h1>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Store In Firebase</p>
        <div id="firebaseui-auth-container"></div>
        <button @click="logout">Logout</button>
    </div>
</template>
<script>
import { useUserStore } from '../stores/userStore'; // 引入 userStore
import { getApps, initializeApp } from "firebase/app";
import { getAuth } from "firebase/auth";
import * as firebaseui from "firebaseui";
import { GoogleAuthProvider, EmailAuthProvider, GithubAuthProvider, PhoneAuthProvider } from "firebase/auth";
import { onAuthStateChanged } from "firebase/auth";
import LoginTutorial from '../components/tutorials/LoginTutorial.vue';
export default {
  components: {
    LoginTutorial,
},
    name: "FirebaseLogin",
    setup() {
        const user = useUserStore(); // 使用 userStore
        return { user };
    },
    mounted() {
        try {

            const firebaseConfig = {
                apiKey: "AIzaSyCVEr6ULFvAwmAmV3UMlu80kkY6BQ2ifDc",
                authDomain: "idncreater.firebaseapp.com",
                projectId: "idncreater",
                storageBucket: "idncreater.appspot.com",
                messagingSenderId: "760391243578",
                appId: "1:760391243578:web:b2f6ed0bfba9f6c3952203",
                measurementId: "G-R5JC1S3W1F",
            };
            //console.log("Before initializing Firebase");
            if (!getApps().length) {
                initializeApp(firebaseConfig);
            }
            //console.log("After initializing Firebase");
            const uiConfig = {
                signInSuccessUrl: "/", // 登录成功后要导航到的 URL
                signInOptions: [
                    // 请根据需要启用或禁用以下提供程序
                    {
                        provider: GoogleAuthProvider.PROVIDER_ID,
                        customParameters: {
                            prompt: "select_account",
                        },
                        buttonColor: "#0884d9",
                        
                    },
                    // firebase.auth.FacebookAuthProvider.PROVIDER_ID,
                    // firebase.auth.TwitterAuthProvider.PROVIDER_ID,
                    {
                        provider: GithubAuthProvider.PROVIDER_ID,
                        //requireDisplayName: false,
                    },
                    
                    {
                        provider: EmailAuthProvider.PROVIDER_ID,
                        requireDisplayName: false,
                    },
                    {
                        provider: PhoneAuthProvider.PROVIDER_ID,
                        //requireDisplayName: false,
                    },
                ],
            };
            //console.log("Before creating FirebaseUI instance");
            const auth = getAuth();


            onAuthStateChanged(auth, (user) => {
                if (user) {
                    const idToken = user ? user.getIdToken() : null;
                    console.log("User is signed in with email:", user.email);
                    console.log("Auth object:", idToken);

                    this.user.setUserInfo({
                        name: '',
                        email: user.email,
                        avatar: '',
                        auth: this.auth,
                        idToken: idToken
                    }); // 设置 userInfo
                } else {
                    console.log("User is signed out");
                }
            });
            let ui;

            if (!firebaseui.auth.AuthUI.getInstance()) {
                ui = new firebaseui.auth.AuthUI(auth);
            } else {
                ui = firebaseui.auth.AuthUI.getInstance();
                ui.reset();
            }
            //console.log("After creating FirebaseUI instance");

            ui.start("#firebaseui-auth-container", uiConfig);
        } catch (error) {
            console.error("Error during mounted hook:", error);
        }
    },
    methods: {
        logout() {
            const auth = getAuth();
            // 调用注销方法，清除用户登录信息
            auth().signOut().then(() => {
                // 注销成功
            }).catch(() => {
                // 注销失败
            });
            this.user.userInfo = {
                name: '',
                email: '',
                avatar: '',
                auth: '',
                token: ''
            }

        }
    }
    }
</script>
<style scoped>
button {
    background-color: #0075e3;
    border: none;
    color: white;
    padding: 0.5em 1em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 0.8em;
    cursor: pointer;
    border-radius: 1em;
    box-shadow: 0 4px #999;
    transition-duration: 0.4s;
    position: fixed;
    top: 35px;
    right: 10px;
    z-index: 999;
}

button:hover {
    box-shadow: 0 8px #999;
    transform: translateY(-2px);
}
</style>




  